import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CategoriesService } from './categories.service';
import { Company } from '../models/company';
import { Router } from '@angular/router';
import { MessageService } from './message.service';

@Injectable({
  providedIn: 'root'
})
export class CompanyService {


  public wrapper = {
    companyId: null,
    company: null,
    companies: null,
    userId: null,
    coupon: null,
    coupons: null,
    couponId: null,
    purchases: null,
    user: null,
    token: null,
    message : null
  }

  public categories: string[] = this.myCategories.categories;

  constructor(private http: HttpClient, private myCategories: CategoriesService, private router: Router, private messageService : MessageService) {
    this.wrapper.company = new Company();
  }

  public createCompany(company: Company, token: string): void {
    let observable = this.http.post<Company>(`http://localhost:8080/companies/?token=${token}`, company);
    observable.subscribe(
      res => {
        console.log("Create company success !\n", this.wrapper.company);
        this.messageService.wrapper.message= `Company '${company.name}' created successfully!`;
        this.router.navigate(["/message"]);
      }, err => {
        alert("Create company faild!");
        alert("Error: " + err.status + ", Message: " + err.error.message);

      });
  }

  public getCompany(companyId: number, token: string): Company {
    let observable = this.http.get<Company>(`http://localhost:8080/companies/${companyId}?token=${token}`);
    observable.subscribe(res => {
      this.wrapper.company = res;
      console.log("Company " + companyId + "geted successfully!");
      console.log(this.wrapper.company);
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
    return this.wrapper.company;
  }

  public deleteCompany(company: Company, token: string): void {
    let observable = this.http.delete<Company>(`http://localhost:8080/companies/${company.id}?token=${token}`);
    observable.subscribe(res => {
      console.log("Company " + company.name + " deleted  successfully!");
      this.messageService.wrapper.message= "Company'" + company.name + "' deleted  successfully!";
      this.router.navigate(["/message"]);
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
  }

  public updateCompany(company: Company, token: string): void {
    let observable = this.http.put<Company>(`http://localhost:8080/companies?token=${token}`, company);
    observable.subscribe(res => {
      console.log("Company " + company.id + "updated successfully!");
      this.messageService.wrapper.message= "Company'" + company.name + "'updated successfully!";
      this.router.navigate(["/message"]);
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
  }


  public getAllCompanies(token: string): Company[] {
    let observable = this.http.get<Company[]>(`http://localhost:8080/companies/?token=${token}`);
    observable.subscribe(res => {
      this.wrapper.companies = res;
      console.log("Companies geted successfully!");
      console.log(this.wrapper.companies);
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
    return this.wrapper.companies;
  }

  public getUserName(): string {
    return this.wrapper.user.userName;
  }


}
