import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Coupon } from '../models/coupon';

@Injectable({
  providedIn: 'root'
})
export class CouponService {

  public wrapper = {
    coupon: null,
    coupons: null,
    couponPurchases: null
  }

  constructor(private http: HttpClient) { }

  public getAllCoupons(token: string): Coupon[] {
    let observable = this.http.get<Coupon[]>(`http://localhost:8080/coupons?token=${token}`);
    observable.subscribe(
      res => {
        this.wrapper.coupons = res;
        console.log("get Coupon success ! \n number of coupons: " + this.wrapper.coupons.length);
        console.log(this.wrapper.coupons);
        this.wrapper.coupons = res;
        return res;
      }, err => {
        this.wrapper.coupons = null;
        alert("get coupons failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
    return this.wrapper.coupons
  }

  public getCouponsByCompany(companyId: number, token: string): void {
    let observable = this.http.get<Coupon[]>(`http://localhost:8080/coupons/byCompany?id=${companyId}&token=${token}`);
    observable.subscribe(
      res => {
        this.wrapper.coupons = res;
        console.log("get Coupon success ! \n number of coupons: " + this.wrapper.coupons.length);
        console.log(this.wrapper.coupons);
        this.wrapper.coupons = res;

      }, err => {
        this.wrapper.coupons = null;
        alert("get coupons failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
  }

  public getCouponsByCompanyAndCategory(category: string, companyId: number, token: string): void {
    let observable = this.http.get<Coupon[]>(`http://localhost:8080/coupons/byCompany/category?id=${companyId}&category=${category}&token=${token}`);
    observable.subscribe(
      res => {
        this.wrapper.coupons = res;
        console.log("get By Company And Category Coupon success ! \n number of coupons: " + this.wrapper.coupons.length);
        console.log(this.wrapper.coupons);
        this.wrapper.coupons = res;

      }, err => {
        this.wrapper.coupons = null;
        alert("get coupons failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
  }

  public getCouponsByCategory(category: string, token: string): void {
    let observable = this.http.get<Coupon[]>(`http://localhost:8080/coupons/byCategory?category=${category}&token=${token}`);
    observable.subscribe(
      res => {
        this.wrapper.coupons = res;
        console.log("get By Company And Category Coupon success ! \n number of coupons: " + this.wrapper.coupons.length);
        console.log(this.wrapper.coupons);
        this.wrapper.coupons = res;

      }, err => {
        this.wrapper.coupons = null;
        alert("get coupons failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
  }

  public getCouponsByCompanyAndMaxPrice(maxPrice: number, companyId: number, token: string): void {
    let observable = this.http.get<Coupon[]>(`http://localhost:8080/coupons/byCompany/maxPrice?id=${companyId}&maxPrice=${maxPrice}&token=${token}`);
    observable.subscribe(
      res => {
        this.wrapper.coupons = res;
        console.log("get Coupon By Company And Max Price success ! \n number of coupons: " + this.wrapper.coupons.length);
        console.log(this.wrapper.coupons);
        this.wrapper.coupons = res;

      }, err => {
        this.wrapper.coupons = null;
        alert("get coupons failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
  }

  public getCouponsByMaxPrice(maxPrice: number, token: string): void {
    let observable = this.http.get<Coupon[]>(`http://localhost:8080/coupons/byMaxPrice?maxPrice=${maxPrice}&token=${token}`);
    observable.subscribe(
      res => {
        this.wrapper.coupons = res;
        console.log("get Coupon By Company And Max Price success ! \n number of coupons: " + this.wrapper.coupons.length);
        console.log(this.wrapper.coupons);
        this.wrapper.coupons = res;

      }, err => {
        this.wrapper.coupons = null;
        alert("get coupons failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
  }

  public getCouponsByCustomer(ustomerId: number, token: string): Coupon[] {
    let observable = this.http.get<Coupon[]>(`http://localhost:8080/coupons/byCustomer?id=${ustomerId}&token=${token}`);
    observable.subscribe(
      res => {
        this.wrapper.couponPurchases = res;
        console.log("get Coupon success ! \n number of coupons: " + this.wrapper.couponPurchases.length);
        console.log(this.wrapper.couponPurchases);
        return this.wrapper.couponPurchases;
      }, err => {
        this.wrapper.couponPurchases = null;
        alert("get coupons failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
    return this.wrapper.couponPurchases;

  }

  public createCoupon(token: string, coupon: Coupon): void {
    let observable = this.http.post<Coupon>(`http://localhost:8080/coupons/?token=${token}`, coupon);
    observable.subscribe(res => {
      console.log("coupon No: " + res + " crated successfully!");
    }, err => {
      alert("Error: " + err.status + ",\n Message: " + err.error.message);
    });
  }

  public updateCoupon(token: string, coupon: Coupon): void {
    let observable = this.http.put<Coupon>(`http://localhost:8080/coupons/?token=${token}`, coupon);
    observable.subscribe(res => {

      console.log("coupon No: " + coupon.id + "updated successfully!");
    }, err => {
      alert("Error: " + err.status + "\n Update faild.\n Message: " + err.error.message);
    });
  }

  public deleteCoupon(id: number, token: string): void {
    let observable = this.http.delete<Coupon>(`http://localhost:8080/coupons/${id}/?token=${token}`);
    observable.subscribe(res => {
      console.log("coupon No: " + id + " deleted successfully!");

    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
  }

  public getCoupon(id: number, token: string): Coupon {
    let observable = this.http.get<Coupon>(`http://localhost:8080/coupons/${id}?token=${token}`);
    observable.subscribe(
      res => {
        this.wrapper.coupon = res;
        console.log("get Coupon  success !");
        console.log(this.wrapper.coupon);
        this.wrapper.coupon = res;

      }, err => {
        this.wrapper.coupons = null;
        alert("get coupon failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
    return this.wrapper.coupon;
  }

}
