import { Injectable } from '@angular/core';
import { Coupon } from '../models/coupon';

@Injectable({
  providedIn: 'root'
})
export class CartService {

  constructor() {
  }

  public removeFromCart(coupon: Coupon) {
    let couponsArray = this.getLocal();
    let index = couponsArray.indexOf(coupon);
    couponsArray.splice(index, 1);
    localStorage.setItem("coupons", this.setLocal(couponsArray));
  }

  public addToCart(c: Coupon) {
    let couponsArray = this.getLocal();

    if (couponsArray == null) {
      couponsArray = [];
    }

    couponsArray.push(c);

    let couponsStr = JSON.stringify(couponsArray);

    localStorage.setItem("coupons", couponsStr);
  }

  public setLocal(couponsArray: Coupon[]) {
    let couponsStr = JSON.stringify(couponsArray);
    return couponsStr;
  }

  public getLocal() {
    let couponStr = localStorage.getItem("coupons");
    let couponsObj = <Coupon[]>JSON.parse(couponStr);
    return couponsObj;
  }

  public getCountItems(): number {
    let couponsArray = this.getLocal();
    return couponsArray.length;
  }
}
