import { Injectable } from '@angular/core';
import { Customer } from '../models/customer';
import { HttpClient } from '@angular/common/http';
import { Purchase } from '../models/purchase';
import { Coupon } from '../models/coupon';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class CustomerService {

  public userId: string;
  public customerId: number;
  public customer: Customer;
  public purchase: Purchase;
  public token: string;
  public coupon: Coupon;
  public coupons: Coupon[];

  constructor(private http: HttpClient, private router: Router) {
  }

  public CustomerRegister(customer: Customer) {
    let observable = this.http.post<Customer>("http://localhost:8080/customers", customer);
    observable.subscribe(res => {
      alert("Your register is complite")
      console.log("Register successful \n");
      this.router.navigate(["/login"]);
    }, err => {
      alert("Register failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
  }

  public updateCustomer(customer: Customer, token: string) {
    let observable = this.http.put<Customer>(`http://localhost:8080/customers?token=${token}`, customer);
    observable.subscribe(res => {
      alert("Your update is complite")
      console.log("Update customer successful \n");
      this.router.navigate(["/customer"]);
    }, err => {
      alert("Update failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
  }

  public getCustomer(id: number, token: string): Customer {
    let observable = this.http.get<Customer>(`http://localhost:8080/customers/${id}?token=${token}`); {
      observable.subscribe(res => {
        this.customer = res;

        console.log(this.customer);
      },
        err => {
          alert("Error: " + err.status + ", Message: " + err.error.message);
        })
    }
    return this.customer;
  }


  deleteCustomer(id: number, token: string) {
    let observable = this.http.delete<Customer>(`http://localhost:8080/customers/${id}?token=${token}`);
    observable.subscribe(res => {
      alert("Customer delete successfully")
      console.log("Customer delete successfully");
    }, err => {
      alert("delete failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });  }


}
