import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Purchase } from '../models/purchase';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class PurchaseService {

  public wrapper = {
    purchase: null,
    purchases: null,
  }

  constructor(private http: HttpClient, private router: Router) {
  }

  public createPurchase(token: string, purchase: Purchase): number {
    let observable = this.http.post<Purchase>(`http://localhost:8080/purchases/?token=${token}`, purchase);
    observable.subscribe(res => {
      console.log("the purchase is: " + res);
      localStorage.setItem("purchase", JSON.stringify(res));
      this.router.navigate(["/thankyou"]);
      return res;
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
    return null;
  }

  public getPurchase(purchaseId: number, token: string): Purchase[] {
    let observable = this.http.get<Purchase>(`http://localhost:8080/purchases/${purchaseId}?token=${token}`);
    observable.subscribe(res => {
      this.wrapper.purchase = res;
      console.log(`purchase no'${res.purchaseId} geted successfully !`);
      console.log(this.wrapper.purchases);
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
    return this.wrapper.purchase;
  }

  public getPurchasesByCompany(companyId: number, token: string): Purchase[] {
    let observable = this.http.get<Purchase>(`http://localhost:8080/purchases/byCompany?id=${companyId}&token=${token}`);
    observable.subscribe(res => {
      this.wrapper.purchases = res;
      console.log("get purchases success ! \nnumber of purchases: " + this.wrapper.purchases.length);
      console.log(this.wrapper.purchases);
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
    return this.wrapper.purchases;
  }

  public getPurchasesByCustomer(customerId: number, token: string): Purchase[] {
    let observable = this.http.get<Purchase>(`http://localhost:8080/purchases/byCustomer?id=${customerId}&token=${token}`);
    observable.subscribe(res => {
      this.wrapper.purchases = res;
      console.log("get purchases success ! \nnumber of purchases: " + this.wrapper.purchases.length);
      console.log(this.wrapper.purchases);
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
    return this.wrapper.purchases;
  }

}
