import { Injectable } from '@angular/core';
import { User } from '../models/user';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { LoggedData } from '../models/loggedData';
import { MessageService } from './message.service';

@Injectable({
  providedIn: 'root'
})

export class UserService {

  public loggedData: LoggedData;
  public user: User;
  public users: User[];
  public companyUsers: User[];
  public isCompany: boolean;
  public isCustomer: boolean;
  public isAdmin: boolean;

  constructor(private http: HttpClient, private router: Router ,  private messageService : MessageService) {
  }

  public login(user: User): void {
    let observable = this.http.post<LoggedData>("http://localhost:8080/users/login", user);
    observable.subscribe(res => {

      this.loggedData = res;

      alert("Login successful");

      if (this.loggedData.userType == 'COMPANY') {
        this.isCompany = true;
        this.router.navigate(["/company"]);
      }
      else if (this.loggedData.userType == 'CUSTOMER') {
        this.isCustomer = true;
        this.router.navigate(["/customer"]);
      }
      else {
        this.isAdmin = true;
        this.router.navigate(["/admin"]);
      }

      sessionStorage.setItem("token", this.loggedData.userToken);
      sessionStorage.setItem("id", this.loggedData.userId + "");
      sessionStorage.setItem("companyId", this.loggedData.companyId + "");

    }, err => {
      alert("Login failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
  }

  public gatUser(id: number, token: string): User {
    let observable = this.http.get<User>(`http://localhost:8080/users/${id}?token=${token}`);
    observable.subscribe(res => {
      this.user = res;
      console.log("Get user successfully \n");
      console.log(this.user);

    }, err => {
      alert("Get user failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
    return this.user;
  }

  public getAllUsers(token: string): User[] {
    let observable = this.http.get<User[]>(`http://localhost:8080/users?token=${token}`);
    observable.subscribe(res => {
      this.users = res;
      console.log("Get users successfully \n");
      console.log(this.users);

    }, err => {
      alert("Get user failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
    return this.users;
  }

  public getCompanyUsers(companyId: number, token: string): User[] {
    let observable = this.http.get<User[]>(`http://localhost:8080/users/byCompany?id=${companyId}&token=${token}`);
    observable.subscribe(res => {
      this.companyUsers = res;
      console.log("Get company users successfully \n");
      console.log(this.companyUsers);

    }, err => {
      alert("Get company users failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
    return this.companyUsers;

  }

  public addUser(user: User, token: string) {
    let observable = this.http.post<User>(`http://localhost:8080/users?token=${token}`, user);
    observable.subscribe(res => {
      console.log("User added successfully \n");
      this.messageService.wrapper.message= `User  No ${res} created successfully!`;
      this.router.navigate(["/message"]);
    }, err => {
      alert("Add user ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
  }

  public updateUser(user: User, token: string) {
    let observable = this.http.put<User>(`http://localhost:8080/users/?token=${token}`, user);
    observable.subscribe(res => {
      alert("Updated successfully")
      console.log("User updated successfully");
      this.router.navigate(["/login"]);
    }, err => {
      alert("Update user failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
  }

  public deleteUser(user: User, token: string) {
    let observable = this.http.delete<User>(`http://localhost:8080/users/${user.id}?token=${token}`);
    observable.subscribe(res => {
      console.log("User deleted successfully");
      this.messageService.wrapper.message= `User  No ${user.id} deleted successfully!`;
      this.router.navigate(["/message"]);
    }, err => {
      alert("Delete user failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
  }

  public signUot(): void {
    sessionStorage.clear();
    this.user = undefined;
    alert("signUot successful");
    this.router.navigate(["/home"]);
  }

}
