import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { CompanyService } from 'src/app/shared/services/company.service';
import { MessageService } from 'src/app/shared/services/message.service';


@Component({
  selector: 'app-masage',
  templateUrl: './masage.component.html',
  styleUrls: ['./masage.component.css']
})
export class MasageComponent implements OnInit {


  public message: string;

  constructor(private location: Location, private messageService : MessageService) {
    this.message = messageService.wrapper.message;
  }

  backClicked() {
    this.location.back();
  }


  ngOnInit() {
  }

}
