import { Injectable } from '@angular/core';
import { Discount } from '../models/discount';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class DiscountService {

  public couponCode: Discount ;
  public couponCods: Discount[] ;


  constructor(private http: HttpClient) { }


  public setCouponCode(code: Discount , token: string) {
    let observable = this.http.post<Discount>(`http://localhost:8080/discountes/?token=${token}`, code);
    observable.subscribe(res => {
      alert("Your code is complite")
      console.log("code successful \n");
    }, err => {
      alert("code failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
    });
  }

  public getCouponCode(code: string, token: string): Discount {
    let observable = this.http.get<Discount>(`http://localhost:8080/discountes/${code}?token=${token}`);
    observable.subscribe(
      res => {
        this.couponCode = res;
        console.log("get Discount  success !");
        console.log(this.couponCode);
      }, err => {
        alert("get Discount failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
    return this.couponCode;
  }

  public getCouponCodes(token: string): Discount[] {
    let observable = this.http.get<Discount[]>(`http://localhost:8080/discountes?token=${token}`);
    observable.subscribe(
      res => {
        this.couponCods = res;
        console.log("get Discounts  success !");
        console.log(this.couponCods);
      }, err => {
        alert("get Discounts failed ! Error: \n" + err.status + "\n Message: " + err.error.message);
      });
    return this.couponCods;
  }

  public deleteDiscount(code: string, token: string): void {
    let observable = this.http.delete<Discount>(`http://localhost:8080/discountes/${code}?token=${token}`);
    observable.subscribe(res => {
      console.log("Discount No: " + code + " deleted successfully!");
    }, err => {
      alert("Error: " + err.status + ", Message: " + err.error.message);
    });
  }

}
