import { Component, OnInit, Input } from '@angular/core';
import { Coupon } from '../../shared/models/coupon';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { UserService } from '../../shared/services/user.service';
import { CategoriesService } from '../../shared/services/categories.service';
import { User } from '../../shared/models/user';
import { CompanyService } from '../../shared/services/company.service';
import { Company } from '../../shared/models/company';
import { CouponService } from '../../shared/services/coupon.service';
import { PurchaseService } from '../../shared/services/purchase.service';
import { MatButtonModule } from '@angular/material/button';
import { DiscountService } from 'src/app/shared/services/coupon-cods.service';
import { Discount } from 'src/app/shared/models/discount';

@Component({
  selector: 'app-company',
  templateUrl: './company.component.html',
  styleUrls: ['./company.component.css']
})
export class CompanyComponent implements OnInit {

  public user: User;
  public userId: number;
  public companyId: number;
  public couponId: number;
  public coupon: Coupon = new Coupon();

  public tempUsername: string;
  public tempPassword: any;
  public selected: string;

  public maxPrice: number;
  public category: string;

  public companyDeatails: Company = null;
  public token: string;
  public coupons: Coupon[] = [];
  public index: number = 1;
  public text: string;

  
  public isCreate: boolean = false;
  public isUpdate: boolean = false;
  public isShowCoupons: boolean = false;
  public isShowPurchases: boolean = false;
  public isCreated: boolean = false;
  public isUpdated: boolean = false;
  public isDelete: boolean = false;
  public isDeleted: boolean = false;
  public isUpdateDetails: boolean = false;
  public showUpdateDetails: boolean = false;
  public categories: string[] = this.myCategories.categories;

  constructor(private myCompanyService: CompanyService, private myCouponService: CouponService, private myPurchaseService: PurchaseService, private http: HttpClient, private router: Router, private myUserService: UserService, private myCategories: CategoriesService) {
    this.token = sessionStorage.getItem("token");
    this.userId = parseInt(sessionStorage.getItem("id"));
    this.companyId = parseInt(sessionStorage.getItem("companyId"));
    this.user = new User();
  }

  public showCoupons(): void {
    this.myCouponService.getCouponsByCompany(this.companyId, this.token);
    this.showFormCoupons();
  }

  public showCouponsByCategory(): void {
    this.myCouponService.getCouponsByCompanyAndCategory(this.category, this.companyId, this.token);
    this.showFormCoupons();
  }

  public showCouponsByMaxPrice(): void {
    this.myCouponService.getCouponsByCompanyAndMaxPrice(this.maxPrice, this.companyId, this.token);
    this.showFormCoupons();
  }

  public createCoupon(): void {
    this.coupon.inStock=false;
    this.myCouponService.createCoupon(this.token, this.coupon);
    this.text = "coupon created successfully!";
    this.isCreate = false;
    this.isCreated = true;
  }

  public updateCoupon(): void {
    this.myCouponService.updateCoupon(this.token, this.coupon);
    this.text = "coupon updated successfully!";
    this.isUpdated = true;
    this.isUpdate = false;
  }

  public deleteCoupon(): void {
    this.myCouponService.deleteCoupon(this.coupon.id, this.token);
    this.text = "coupon deleted successfully!";
    this.isDeleted = true;
    this.isDelete = false;
  }

  public getPurchases(): void {
    this.myPurchaseService.getPurchasesByCompany(this.companyId, this.token);
    this.showFormPurchases();
  }

  public updateUserDetails() {
    this.user.type="COMPANY";
    this.myUserService.updateUser(this.user, this.token);
    this.myUserService.gatUser(this.userId, this.token);
    this.showUpdateDetails = false;
    this.text = "User updated successfully!";
  }

  private validateUserNameAndPassword() {
    if (this.tempUsername != undefined && this.tempPassword != undefined) {
      if (this.tempUsername == this.myUserService.user.userName && this.tempPassword == this.myUserService.user.password) {
        this.user.id = this.userId;
        this.tempUsername = undefined;
        this.tempPassword = undefined;
        this.isUpdateDetails = false;
        this.showUpdateDetails = true;
      } else {
        alert("You insert wrong details");
      }
    } else {
      alert("you must fill the inbox");
    }
  }

  public showUserDetails() {
    this.isUpdateDetails = true;
    this.showUpdateDetails = false;
    this.isCreate = false;
    this.isCreated = false;
    this.isUpdate = false;
    this.isUpdated = false;
    this.isDelete = false;
    this.isDeleted = false;
    this.isShowCoupons = false;
    this.isShowPurchases = false;
  }

  public showFormCreate() {
    this.coupon = new Coupon();
    this.isCreate = true;
    this.isCreated = false;
    this.isDelete = false;
    this.isDeleted = false;
    this.isUpdate = false;
    this.isUpdated = false;
    this.isShowCoupons = false;
    this.isShowPurchases = false;
    this.isUpdateDetails = false;
  }

  public showFormUpdate(c: Coupon) {
    this.coupon = c;
    this.isUpdate = true;
    this.isCreate = false;
    this.isCreated = false;
    this.isUpdated = false;
    this.isDelete = false;
    this.isDeleted = false;
    this.isShowCoupons = false;
    this.isShowPurchases = false;
    this.isUpdateDetails = false;
  }

  public showFormDelete(coupon: Coupon) {
    this.coupon = coupon;
    this.isDelete = true;
    this.isDeleted = false;
    this.isUpdate = false;
    this.isUpdated = false;
    this.isCreate = false;
    this.isCreated = false;
    this.isShowCoupons = false;
    this.isShowPurchases = false;
    this.isUpdateDetails = false;
  }

  private showFormCoupons() {
    this.isShowCoupons = true;
    this.isCreate = false;
    this.isCreated = false;
    this.isUpdate = false;
    this.isUpdated = false;
    this.isDelete = false;
    this.isDeleted = false;
    this.isShowPurchases = false;
    this.isUpdateDetails = false;
  }

  private showFormPurchases() {
    this.isShowPurchases = true;
    this.isCreate = false;
    this.isCreated = false;
    this.isUpdate = false;
    this.isUpdated = false;
    this.isDelete = false;
    this.isDeleted = false;
    this.isShowCoupons = false;
    this.isUpdateDetails = false;
  }

  ngOnInit() {
    this.myUserService.gatUser(this.userId, this.token);
    this.myCompanyService.getCompany(this.companyId, this.token);
    this.myUserService.isCompany = true;
  }

}
